import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";
import Math2 from '../../data/math2';

const mouse = new THREE.Vector2();

const l1 = 5;
const l2 = 5;
var tt = 0;

export default class IkArm {

    constructor(scene) {

        this.createArms(scene);
        this.createTarget(scene);
        this.createRaycast();

        scene.add(new THREE.Mesh(new THREE.BoxGeometry(100, 0.2, 0.2), new THREE.MeshStandardMaterial({color: new THREE.Color(0xff0000)})));
        scene.add(new THREE.Mesh(new THREE.BoxGeometry(0.2, 0.2, 100), new THREE.MeshStandardMaterial({color: new THREE.Color(0x105099)})));
    }

    update(scene, camera){


        let pos = this.getPlaneXZ(scene, camera);
        this.target.position.set(pos.x, 0, pos.z);

        //this.target.position.set(5*Math.cos(tt/2), 5*Math.sin(tt/2), 0);

        this.target.position.set(pos.x, 0, pos.z);

        let q2 = this.getQ2(this.target.position);
        let q1 = this.getQ1(this.target.position, q2);


        this.arm1.rotation.y = -q1;
        this.arm2.rotation.y = -q2;

    }

    createArms(scene){

        const _material = new THREE.MeshStandardMaterial({color: 0xf6ff96});

        /* ******************************************* */

        const _geoBox1 = new THREE.BoxGeometry(l1, 0.1, 0.5);
        const box1 = new THREE.Mesh( _geoBox1, _material );
        box1.position.set(2.5, 0, 0);

        this.arm1 = new THREE.Object3D();
        this.arm1.add(box1);


        /* ******************************************* */
        const _geoBox2 = new THREE.BoxGeometry(l2, 0.1, 0.5);
        const box2 = new THREE.Mesh(_geoBox2, new THREE.MeshStandardMaterial({color: 0x96e1ff}));
        box2.position.set(2.5, 0, 0);

        this.arm2 = new THREE.Object3D();
        this.arm2.position.set(5, 0, 0);
        this.arm2.add(box2);

        this.arm1.add(this.arm2);


        scene.add(this.arm1);


    }

    createTarget(scene){

        this.target = new THREE.Mesh(new THREE.SphereGeometry(0.5, 32, 32), new THREE.MeshNormalMaterial());
        scene.add(this.target);

    }

    createRaycast(){

        this.raycaster = new THREE.Raycaster();

        function onMouseMove(e){
            mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
            mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;
        }

        window.addEventListener( 'mousemove', onMouseMove, false );
    }

    getPlaneXZ(scene, camera){

        this.raycaster.setFromCamera( mouse, camera );

        const intersects = this.raycaster.intersectObjects( scene.children );

        let pos = new THREE.Vector2(0, 0);
        for ( let i = 0; i < intersects.length; i ++ ) {
            let obj = intersects[i];

            if(obj.object.name == 'plane')
                return pos = obj.point;
    	}

        return pos;
    }

    getQ1(pos, q2){


        const x1 = pos.x;
        const x2 = pos.z;

        /*const div1 = Math.atan2(x2, x1);
        const div2 = Math.atan2(l2*Math.sin(q2), l1+l2*Math.cos(q2));

        const q1 = div1 - div2;*/

        const q1 =  Math.atan(x2/x1) - Math.atan(l2*Math.sin(q2)/(l1+l2*Math.cos(q2)));

        return q1;
    }

    getQ2(pos){

        const x1 = pos.x;
        const x2 = pos.z;



        /*const d2 = Math.pow(x1, 2) - Math.pow(x2, 2);
        const div = (Math.pow(l1, 2) + Math.pow(l2, 2) - d2)/(2*l1*l2);
        const q2 = Math.PI - Math.acos(div);*/

        const q2 = Math.PI - Math.acos((l1*l1+l2*l2-x1*x1-x2*x2)/(2*l1*l2));


        return q2;
    }
}
